.container {
  display: flex;
  justify-content: center;
}

.childrenContainer {
  width: 480px;
  box-shadow: 0px 0px 5px #393939;
  min-height: 100vh;
  box-sizing: border-box;
  @media (max-width: 480px) {
    box-sizing: border-box;
    width: 100%;
    min-height: calc(100vh - 57px);
  }
}
